.landing-page {
  /* background-image: url("../images/main-back.svg");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover; */
  padding: 20px 5%;
  min-height: 80vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background-color: #eee;
}

.landing-page .text-content {
  padding: 5% 10% 0 10%;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.text-content h1.bismillah {
  color: #f11a7b;
  font-size: 38px;
  margin-bottom: 40px;
  position: relative;
  display: flex;
  justify-content: center;
}
.text-content h1.bismillah::after {
  content: "";
  display: block;
  position: absolute;
  width: 100px;
  height: 5px;
  background-color: #f11a7b;
  bottom: -10px;
}
.text-content h1 {
  font-family: "Montserrat", sans-serif;
  color: gray;
  line-height: 1.5;
  margin-bottom: 20px;
}
.text-content p {
  font-family: "Poppinis", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.7);
}
.text-content a {
  display: block;
  width: 140px;
  margin: auto;
  background-color: #793fdf;
  color: #fff;
  padding: 15px;
  text-decoration: none;
  text-transform: uppercase;
  border-radius: 50px;
  font-family: "Montserrat", sans-serif;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.3);
  margin-top: 20px;
  font-size: 14px;
  font-weight: 700;
}
@media (min-width: 740px) {
  .landing-page .text-content {
    padding: 2% 20% 0 20%;
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
}
@media (max-width: 740px) {
  div.landing-page {
    margin-top: 0px;
  }
  .text-content h1 {
    font-size: 24px;
  }

  .text-content a {
    display: block;
    width: 110px;
    margin: auto;
    background-color: #793fdf;
    color: #fff;
    padding: 8px;
    text-decoration: none;
    text-transform: uppercase;
    border-radius: 50px;
    font-family: "Montserrat", sans-serif;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.3);
    margin-top: 20px;
    font-size: 12px;
    font-weight: 400;
  }
}
